@import "styles/sass/main";

.Table {
  &__tr {
    display: flex;
    column-gap: 3px;
    width: 100%;
    margin-bottom: 6px;
  }

  &__th {
    display: block;
    width: 44px;
    font-size: 18px;
    font-weight: 600;
  }

  &__button-wrap {
    width: 100%;
    height: 24px;
  }
}